.burger-menu.is-open {
  visibility: visible;
}

.burger-menu {
  visibility: hidden;
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  pointer-events: 0;
  background-color: #f4f4f4;
  padding-top: 24px;
