.closeburgmenu {
  color: var(--txt-col);
  width: 24px;
  height: 24px;
  cursor: pointer;
  z-index: 100;
  transition: fill .25s var(--timing-funcion);
  background-color: #f4f4f4;
  border: none;
  position: relative;
}

.btn-close_icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dark .btn-close_icon {
  fill: #f4f4f4;
}

